/* ALL */
.Volumes_Volumes__1bWWe {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 6rem;
}

.Volumes_Wrapper__1IRyg {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #111;
    border-radius: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 2rem;
    margin-bottom: 3rem;
}
.Volumes_Info__3Knqr {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
}
.Volumes_Content__3nZyU, .Volumes_Buttons__1ZXvB {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.Volumes_Heading__2VLjw {
    color: var(--color-purple);
}

/* VOLUMES */
.Volumes_Name__Ry_HU {
    width: 25rem;
}
.Volumes_Driver__1_Thq {
    width: 10rem;
}
.Volumes_Mountpoint__2p-tT {
    width: 40rem;
}

.Volumes_Name__Ry_HU, .Volumes_Driver__1_Thq, .Volumes_Mountpoint__2p-tT {
    overflow: hidden;
    margin-right: 1.25rem;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.Volumes_Button__3_We4 {
    margin-right: 1.25rem !important;
}

.Volumes_Headline__-kCBR {
    color: var(--color-purple);
}

.Volumes_Volumes__1bWWe h1,
.Volumes_Volumes__1bWWe h2 {
    font-weight: 500;
}

.Volumes_Volumes__1bWWe h1 {
    font-size: 1.8rem;
    margin-top: 0;
    margin-bottom: 0.8rem;
}

.Volumes_Volumes__1bWWe h2 {
    font-size: 1.6rem;
}

.Volumes_Volumes__1bWWe p {
    font-size: 1.1rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

.Volumes_DetailTitle__1lWRm {
    color: var(--color-purple);
    width: 5.5rem;
}

.Volumes_Error__17w6n {
    color: red;
    white-space: pre;
    text-align: left !important;
}

/* desktop 1200 + */
@media (min-width: 1200px) {
    * {
        /* color: red; */
    }

    /* VOLUMES */
    .Volumes_Name__Ry_HU {
        width: 15rem;
    }
    .Volumes_Driver__1_Thq {
        width: 5rem;
    }
    .Volumes_Mountpoint__2p-tT {
        width: 31rem;
    }

    .Volumes_Volumes__1bWWe {
        margin-top: 10rem;
    }

    .Volumes_Wrapper__1IRyg {
        width: 90%;
        padding: 4.5rem 2rem 5rem 2rem;
    }
    .Volumes_Info__3Knqr {
        flex-direction: column;
        justify-content: space-around;
        margin-top: 4rem;
    }

    .Volumes_Volumes__1bWWe h1 {
        font-size: 2.5rem;
    }

    .Volumes_Volumes__1bWWe p {
        margin-bottom: 1rem;
    }
}

/* tabletLandscape 900 - 1199 */
/* @media (min-width: 900px) and (max-width: 1199px) { */
@media (max-width: 1199px) {
    * {
        /* color: blue; */
    }
    .Volumes_Driver__1_Thq {
        width: 15rem;
    }
    .Volumes_Buttons__1ZXvB {
        margin-top: 1rem;
        margin-left: 1rem;
    }
    .Volumes_DetailTitle__1lWRm {
        margin-left: 1.5rem !important;
    }
}

/* tabletPortrait 600 - 899 */
/* @media (min-width: 600px) and (max-width: 899px) { */
@media (max-width: 899px) {
    * {
        /* color: purple; */
    }
    .Volumes_Mountpoint__2p-tT {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        word-break: break-word;
        white-space: normal;
        text-overflow: unset;
    }
    .Volumes_Buttons__1ZXvB {
        flex-direction: column-reverse;
        /* margin-top: 1rem; */
    }
    .Volumes_Button__3_We4 {
        margin-top: 0.75rem !important;
    }
    .Volumes_DetailTitle__1lWRm {
        margin-left: 1.5rem !important;
    }
}

/* phone < 600 */
@media (max-width: 599px) {
    * {
        /* color: green; */
    }
    .Volumes_Name__Ry_HU {
        width: 100%;
        /* width: fit-content; */
        /* width: auto; */
        word-break: break-word;
        margin-bottom: 1rem;
        margin-right: 0;
    }
    .Volumes_Driver__1_Thq {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
    .Volumes_DetailTitle__1lWRm {
        margin-left: 0.2rem !important;
    }
}

